import { Link, NavLink, useNavigate } from 'react-router-dom';
import './Header.css';
import { useEffect, useState } from 'react';
import LoginForm from './LoginForm';
import { useCookies } from 'react-cookie';

const Header = () => {

    const [loginOpen, setLoginOpen] = useState(false);

    const [isLoggedIn, setIsLoggedIn] = useState(false);

    const [cookies, removeCookie] = useCookies();

    const navigate = useNavigate();

    const logo = process.env.PUBLIC_URL;

    const openLogin = () => {
        setLoginOpen(true);
    }
    const closeLogin = () => {
        setLoginOpen(false);
    }
    const logout = () => {
        removeCookie('accessToken');
        setIsLoggedIn(false);
        alert(' .');
        navigate('/');
    }

    //accessToken    t/f 
    const checkLogin = () => {
        const token = cookies.accessToken;

        if( !token || token ==='undefined') {
            setIsLoggedIn(false);
        } else{            
            setIsLoggedIn(true);
        }

    }

    //    .     . 
    const needLogin = (e) => {
        checkLogin();
        if(!isLoggedIn) {
            e.preventDefault();
            setLoginOpen(true);
        } else {
            navigate('/write');
        }
    }

    //   ,    
    useEffect(()=>{
        checkLogin();
    },[cookies.accessToken]);

    return (
        <div className='headerCover'>
            <div className='modal'>
                <LoginForm isOpen={loginOpen} closeLogin={closeLogin} />
            </div>
            <div className='header'>
                <div className='navBar'>
                    <Link
                        to='/'
                        className='navLinkItem'>
                            <img src={`${logo}/images/moodlogo_42.png`} alt=''/>
                        </Link>
                    <NavLink
                        to='/write'
                        style={({ isActive }) => ({ fontWeight: isActive ? 'bold' : 'normal' })}
                        className='navLinkItem' onClick={(e)=>{needLogin(e)}}
                    > </NavLink>
                    <NavLink
                        to='/mydiaries'
                        style={({ isActive }) => ({ fontWeight: isActive ? 'bold' : 'normal' })}
                        className='navLinkItem'
                    >  </NavLink>
                    { isLoggedIn ? <div className='navLinkItem' onClick={logout}></div> :<div
                        className='navLinkItem'
                        onClick={openLogin}
                    ></div>}
                </div>
            </div>
        </div>
    );
};
export default Header;