import './Emotions.css';

const Emotions = (props) => {

    const emos = ['', '', '', '', '', '', '', '', '', '', '', '', '', '', ''];
    const emosNeg = ['', '', '', '', '', '', '', '', '', '', '', '', '', '', ''];


    return (
        <>
            <div className='emotionsBox'>
                {props.emotions === 'positive' ?
                    emos.map(emo => {
                        return (
                            <div className='emo'>
                                <span>{emo}</span>
                            </div>
                        );
                    })
                    :
                    emosNeg.map(emo => {
                        return (
                            <div className='emo'>
                                <span>{emo}</span>
                            </div>
                        );
                    })
                }

            </div>
        </>
    );
};

export default Emotions;