import { Link, useNavigate } from 'react-router-dom';
import './Intro.css';
import { useEffect, useState } from 'react';
import { useCookies } from 'react-cookie';
import LoginForm from './LoginForm';


const Intro = () => {
    const [loginOpen, setLoginOpen] = useState(false);
    const [isLoggedIn, setIsLoggedIn] = useState(false);
    const [cookies, removeCookie] = useCookies();
    const navigate = useNavigate();

    const introImg = process.env.PUBLIC_URL;

    const closeLogin = () => {
        setLoginOpen(false);
    }

    //accessToken    t/f 
    const checkLogin = () => {
        const token = cookies.accessToken;
        if (!token || token === 'undefined') {
            setIsLoggedIn(false);
        } else {
            setIsLoggedIn(true);
        }
    }

    //   .     . 
    const needLogin = (e) => {
        checkLogin();
        if (!isLoggedIn) {
            e.preventDefault();
            setLoginOpen(true);
        } else {
            navigate('/write');
        }
    }
    useEffect(() => {
        checkLogin();
    }, [cookies.accessToken]);

    return (
        <div>
            <div className='modal'>
                <LoginForm isOpen={loginOpen} closeLogin={closeLogin} />
            </div>
            <div className='introCover'>
                <div className='introImg'>
                    <img src={`${introImg}/images/mainImg_800.png`} alt='' />
                </div>
                <div className='introText'>
                    <h2> #mood, <span className='title'></span> </h2>
                    <p>  ?    ? <br />
                           <span className="highlight"> </span>  <br />
                             <br />
                           ,<br /> <span className="highlight"> </span>   ! </p>
                    <p><Link to='/write' className='introButton' onClick={(e) => { needLogin(e) }}>  &gt;&gt;</Link></p>
                </div>
            </div>
        </div>
    );
};

export default Intro;